  }
  .goti-path.green {
    bottom: 1px;
    left: calc(162px / 2);
    max-height: calc(162px / 2);
  }
  .path-box {
    width: calc(54px / 4);
    height: calc(54px / 4);
  }
  .goti-path.yellow {
    top: calc(162px / 2);
    left: 0;
    max-width: calc(162px / 2);
  }
  .goti-path.red {
    top: calc(162px / 2);
    right: 0;
    max-width: calc(162px / 2);
  }
  .goti-path.skyblue {
    top: 0;
    left: calc(162px / 2);
    max-height: calc(162px / 2);
  }
  .goti-path.green {
    bottom: 1px;
    left: calc(162px / 2);
    max-height: calc(162px / 2);
  }
  .cheera {
    /* background-size: 75%; */
    z-index: 5;
    align-content: center;
  }
  .victroyBox {
    max-height: calc(164px / 4);
    max-width: calc(164px / 4);
    object-fit: cover;
